import HeaderPage from "../../components/Layout/HeaderPage";
import { Card } from 'semantic-ui-react';
import { FaBox, FaCertificate, FaPallet, FaUser } from "react-icons/fa";
import './Dashboard.scss';
import { useEffect } from "react";
import { getClientesAsync } from "../../actions/clientesActions";
import { getProductosAsync } from "../../actions/productosActions";
import { getCategoriasAsync } from "../../actions/categoriasActions";
import { getSeccionsAsync } from "../../actions/seccionActions";
import { useState } from 'react';

const Dashboard = () => {

  const [cantClientes, setCantClientes] = useState(0);
  const [cantProductos, setCantProductos] = useState(0);
  const [cantCategorias, setCantCategorias] = useState(0);
  const [cantSecciones, setCantSecciones] = useState(0);

  useEffect(() => {
    (async () => {
      const res = await Promise.all([getClientesAsync(), getProductosAsync(), getCategoriasAsync(), getSeccionsAsync()])
      
      if(res[0]) {
        setCantClientes((res[0].data.length));
      }
      if(res[1]) {
        setCantProductos((res[1]).data.length);
      }
      if(res[2]) {
        setCantCategorias((res[2].length));
      }
      if(res[3]) {
        setCantSecciones((res[3].length));
      }

    })()
  }, [])

  return ( 
    <div class="Dashboard">
      <HeaderPage>
        <div className="item ui colhidden">
          <div className="ui big breadcrumb">
            <i className="right chevron icon divider"></i>
            <h2 className="section active">DASHBOARD</h2>
          </div>
        </div>
      </HeaderPage>
      <div className="Dashboard__body app__container_body">
      <div className="app_content__padded_view">
        <Card.Group>
          <Card color="green">
            <Card.Content className="card-resumen">
              <div className="card-resumen__descripcion">
                <FaUser style={{fontSize: 70}}/><br />
              </div>
              <div class="card-resumen__valor">
                <span style={{fontSize: 45}}>{cantClientes}</span><br/>
                <p style={{fontSize: 20}}>Clientes</p>
              </div>
            </Card.Content>
          </Card>

          <Card color="red">
          <Card.Content className="card-resumen">
              <div className="card-resumen__descripcion">
                <FaPallet style={{fontSize: 70}}/><br />
              </div>
              <div class="card-resumen__valor">
                <span style={{fontSize: 45}}>{cantProductos}</span><br/>
                <p style={{fontSize: 20}}>Productos</p>
              </div>
            </Card.Content>
          </Card>

          <Card color="yellow">
            <Card.Content className="card-resumen">
              <div className="card-resumen__descripcion">
                <FaCertificate style={{fontSize: 70}}/><br />
              </div>
              <div class="card-resumen__valor">
                <span style={{fontSize: 45}}>{cantSecciones}</span><br/>
                <p style={{fontSize: 20}}>Secciones</p>
              </div>
            </Card.Content>
          </Card>

          <Card color="blue">
            <Card.Content className="card-resumen">
              <div className="card-resumen__descripcion">
                <FaBox style={{fontSize: 70}}/><br />
              </div>
              <div class="card-resumen__valor">
                <span style={{fontSize: 45}}>{cantCategorias}</span><br/>
                <p style={{fontSize: 20}}>Categorias</p>
              </div>
            </Card.Content>
          </Card>
        </Card.Group>
        </div>
      </div>
    </div>
  );
}

export default Dashboard;